.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
*{
  margin: 0;
  padding: 0;
}
.nav_products{
  background-color:rgb(237, 232, 232);
  height: 75px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.nav_products p{
  font-weight: 500;
  padding-top: 20px;
  font-size: 23px;
  margin-left: 20px;
}
.as{
  margin-top: 23px;
  margin-right: 20px;
  display: none;
  justify-content: space-between;
  width: 190px;
  color: black;
  font-weight: 600;
  height: 60px;
  
}
.carro_icon{
  width: 40px;
  border-bottom: 0;
}  
.as button{
 border: none;
 background-color: rgb(237, 232, 232);
 margin-bottom: 0px;
 font-weight: 700;
 height: 10px;


}

.logo{
  height: 60px;
  width: 60px;
  margin-left: 20px;
  border-radius: 50px;
  margin-top: 6px;
}
.productos{
  display: flex;
  flex-direction: column;
  width: 90%;
  margin: auto;
  margin-top:10vh;
  
}
.productos img{
width: 100px;

}
.contenedor_productos{
  background-color: #282c34;
  margin-top: 40px;
  display: flex;
  flex-direction: row;  
}
.letras_productos{
  display: flex;
  flex-direction: column;
  color: white;
 margin-top: 10px;

}
.name{
  font-weight: 400;
  margin-bottom: 5px;
  text-align: left;
  padding-left: 10px;
}
.description{
  font-weight: 100;
  font-size: 10px;
  text-align: left;
  width: 80%;
  color: rgb(212, 211, 209);
  padding-left: 10px;
}
.price{
  font-weight: 400;
  margin-top: 5px;
  text-align: left;
  padding-left: 10px;
}
.add{
  height: 30px;
  margin-top: 33px;
  margin-right: 5px;
  font-size: 10px;
  width: 80px;
  border: none;
  background-color: rgb(77, 205, 77) ;
  font-weight: 700;
  transition: .6s;
}

.carrito{
  margin-top: 100px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  background-color: rgb(203, 200, 200);
 
  
}

.carrito img{
  width: 100px;
  margin-top: 10px;
  margin: auto;
  
}

.contenedor_carrito{
  background-color: #282c34;
  margin: auto;
  margin-top: 15px;
  height: 250px;
  color: white;
  width: 140px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 15px;
}
.name_cart{
  font-size: 20px;
  font-weight: 200;
}
.price_cart{
  font-weight: 100;
  font-size: 15px;
}
  
.contenedor_carrito button{
  width: 70px;
  border: none;
  background-color: rgb(88, 61, 207);

  
  }
.buttons_cart{
  display: flex;
  flex-direction: column;
  align-self: center;
  margin-bottom: 10px;
  margin-top: 10px;
}

.delete_one{
  margin-top: 10px;
  
  
}

.cart_title{
  background-color: black;
  height: 50px;
  margin-top: 40px;
  color: white;
  font-weight: 500;
  font-size: 30px;
  padding-top: 10px;
}
.contenedor_total{
  background-color: black;
  color:white;
  height: 100px;
  width: 100%;
  position: r;
  top: 100%;
}

.contenedor_total h3{
  font-weight: 100;
  padding-top: 25px;
}


.nav_logo{
  display: flex;
  flex-direction: row;
}
.buy{

  margin-bottom: 10px;
 border: none;
 background-color: rgb(77, 205, 77);
 color: white;
 font-size: 13px;
 font-weight: 100;
 width: 80px;
 height: 30px;
 transition: 1s;
 margin-top: 10px;
 color: black;
}
.hamburger{
border: none;
background-color: rgb(237, 232, 232)  ;
margin-right: 15px;
margin-top: 13px;
height: 20px;
}
.mobile_nav{
  display: flex;
  flex-direction: column;
  height: 170px;
  background-color: #282c34;
  display: none;
  

}
.mobile_nav button{
  width: 100%;
  height: 40PX;
  margin: auto;
  border: none;
  font-weight: 700;
  color: white;
  background-color: #282c34;
  border-bottom: 1px solid rgb(42, 41, 41);

}
.mobile_nav_activado{
  display: flex;

}
.no_products{
  font-weight: 400;
}

@media (min-width:720px){
  .productos{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 80%;
  }
  .contenedor_productos{
    display: flex;
    flex-direction: column;
    width: 200px;

  }
  .contenedor_productos img{
    margin: auto;
    width: 130px;
    margin-top: 20px;
  }
  .contenedor_productos button{
    margin: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    transition: 1s;

  }
  .contenedor_productos button:hover{
    background-color:rgb(49, 130, 49) ;
  }
  .buy:hover{
    background-color: rgb(49, 130, 49);
  }
  .add{
  font-weight: 200;
  }
  .buy{

    margin-bottom: 10px;
   border: none;
   background-color: rgb(77, 205, 77);
   color: white;
   font-size: 13px;
   font-weight: 100;
   width: 80px;
   height: 30px;
   transition: 1s;
   margin-top: 10px;
   color: black;
 }
 .as{
  display: flex;
 }
 .hamburger{
  display: none;
 }
 .mobile_nav{
  display: none;
}
}